@charset "utf-8";
*{
  padding: 0px;
  margin: 0px;
}
ul,li,ol{
  list-style: none;
}
h1{
  width: 157px;
  margin: 1px auto;
}
h2{
  font-size: 20px;
  padding: 10px;
  margin: 0px auto;
  display: block;
  width: 336px;
}
.wrapper{
  width: 100%;
}
.product_list{
  width: 100%;
  margin: 0px auto;
  text-align: center;
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: 1px solid #edaced;
  box-shadow:0px 2px 2px rgba(108, 108, 108,.5);
}
.product_list li{
  width: 100%;
  display: flex;
  margin: 0px auto;
  align-items: center;
}
.product_list img{
  width: 30%;
}
.product_list p{
  text-align: left;
  font-size: 13px;
  margin: 0px 8px 0px 8px;
  line-height: 22px;
}
.product_list p:nth-child(2){
  font-weight: 600;
}
.product_list p:nth-child(3){
  font-weight: 400;
  margin-bottom: 10px;
}
.product_list p>input{
  padding: 3px 10px;
  outline: none;
  border: 0px;
  background-color: purple;
  color: #fff;
  text-align: center;
  display: block;
  cursor: pointer;
}
.p_wrapper:not(:last-child){margin-bottom: 5px;}
.p_wrapper label{
  display: inline-block;
  width: 25%;
  text-align: left;
}
.p_wrapper input{
  width: 50%;
}
#p_contain{
  width: 90%;
  margin: 0px auto;
  padding: 0px;
  text-align: center;
}
#p_contain>p>input[type='button']{
  outline: none;
  border: none;
  padding: 2px 10px;
  background-color: purple;
  color: #fff;
  cursor: pointer;
  display: block;
  margin: 0px auto;
  margin: 10px auto 10px auto;
}